import React from 'react';
import Movie from './Movie.js';
const MovieList = (props) => {
    return (
        //get data movie body api then send to movie.js
        props.movies.map((movie, i) => {

            return (

                            <Movie
                                key={i} myImage={movie.poster_path}
                                myTitle={movie.title}
                                myOverview={movie.overview}
                                userVote={movie.vote_average}
                            />
                             
            )
        })

    )
}

export default MovieList;